body {
  min-height: calc(100vh - 180px);
  width: 100%;
  overflow-x: hidden;
  margin-bottom: 80px;
  margin-top: 100px;
  background: url('./bg.jpg');
  /* background-size: cover; */
  background-attachment: fixed;
}

@media screen and (orientation: landscape) {
  body {
    background-size: 100% auto;
  }
}
@media screen and (orientation: portrait) {
  body {
    background-size: auto 100%;
  }
}

nav a {
  color: white;
  text-decoration: none;
}
nav a:hover {
  color: rgba(255,255,255,.5);
}

.vertical-timeline {
  overflow-x: hidden;
}
.vertical-timeline iframe.youtube {
  width: 100%;
  height: auto;
}

.scribbleContainer .button {
  display: inline-block; 
  font-family: sans-serif;
  color: #000000;
  text-decoration: none;
  font-size: 20px;
  line-height: 1;
  font-weight: 700; 
  text-align: center;
  -webkit-transition: all .5s;
  transition: all .5s;
  cursor: pointer;
}
.scribbleContainer .button:hover {
  color: #000000b4;
}
.scribbleContainer .button.scribble {
  padding: 18px 20px 30px 33px;
}

.scribbleContainer { 
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.scribbleContainer svg { 
  position: absolute; 
  top: 0; 
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
}
.scribbleContainer .scribblePath { 
  fill: none; 
  stroke: #000000; 
  stroke-width: 3;
  transition: stroke .5s;
}
.scribbleContainer:hover .scribblePath {
  stroke: #000000b4;
  stroke-dasharray: 100px 1900px;
  animation: dashoffset 4s linear forwards infinite;
}

@keyframes dashoffset {
    from {stroke-dashoffset: 2000px;}
    to {stroke-dashoffset: 0px;}
}
.react-tabs__tab:focus:after {
  content: none !important;
}
.react-tabs__tab:focus {
  box-shadow: none !important;
}
.react-tabs__tab.react-tabs__tab--selected .scribblePath {
  stroke: #000000b4;
  stroke-dasharray: 100px 1900px;
  animation: dashoffset 4s linear forwards infinite;
}
.react-tabs__tab-list {
  display: flex;
  justify-content: center;
  border: none !important;
}
.react-tabs__tab {
  width: 100px;
  background: none !important;
  border: none !important;
}
.vertical-timeline:before {
  left: 22px !important;
  border-radius: 10px;
}
.vertical-timeline-element-icon {
  left: 5px !important;
}
.vertical-timeline-element-content img {
  max-width: 100%;
  max-height: 100vh;
  margin: 0 auto;
}
@media only screen and (min-width: 1170px) {
  .vertical-timeline--two-columns .vertical-timeline-element-icon, .vertical-timeline.vertical-timeline--two-columns:before {
    left: 50% !important;
  }
}


.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
